.nav {
    margin-bottom: 50px;
}

.ul {
    display: flex;
    justify-content: center;
}

.li {
    display: table;
    list-style: none;
}

.a {
    display: table-cell;
    width: 300px;
    background-color: rgb(43, 34, 34);
    color: white;
    text-decoration: none;
    text-align: center;
    vertical-align: middle;
}

.li:first-child {
    margin-right: 50px;
}